
import Vue from "vue";
import lodash from "lodash";
import permissions from "../config/actions-fields.json";

export default Vue.extend({
  name: "Services",

  props: {
    value: {
      required: true,
      default: () => ({})
    },
    showGuarantee: {
      type: Boolean,
      default: false,
      required: false
    },
    disabled: {
      required: false,
      type: Boolean,
      default: false
    },
    oldValue: {
      required: false,
      type: Object,
      default: () => ({})
    },
    newServices: {
      required: false,
      default: () => ({})
    },
    isEdit: {
      required: false,
      default: false,
      type: Boolean
    },
    extraOutputs: {
      required: false,
      default: false
    },
    serviceTypeId: {
      required: false,
      default: null
    }
  },

  data: () => ({
    lodash,
    model: {} as any,
    oldModel: null as any,
    orderServices: {
      extra_positions: {
        values: []
      }
    } as any,
    services: {} as any,
    serviceId: null as any,
    servicesTotalSum: 0 as number
  }),

  watch: {
    value: {
      immediate: true,
      deep: true,
      handler() {
        this.model = this.value;
        this.checkServices();
        if (!this.model.order?.client?.id) {
          this.model.details.services = [];
        }

        if (this.isEdit) {
          this.serviceId = this.model.order.service_type_id;
        } else {
          this.serviceId = this.serviceTypeId;
        }
        this.$forceUpdate();
      }
    },
    newServices: {
      immediate: true,
      deep: true,
      handler() {
        this.orderServices = this.newServices;
      }
    },
    oldValue: {
      immediate: true,
      handler() {
        this.oldModel = this.oldValue;
      }
    },
    totalSum: {
      immediate: true,
      deep: true,
      handler() {
        if (this.model.order.client && this.model.order.client.type) {
          this.$emit("input", this.totalSumWithVat);

          if (!this.isEdit) {
            this.model.details.services = this.dataForSubmit;
          }
        }
      }
    },
    extraVisitsAfter: {
      immediate: true,
      handler() {
        if (this.extraVisitsAfter > 0) {
          this.$emit("extraVisitsAfter", {
            service_id: this.services.extra_visits_after.value,
            sum_no_vat: this.extraVisitsAfterSum,
            sum_with_vat: this.addVat(this.extraVisitsAfterSum),
            quantity: this.extraVisitsAfter
          });
        } else {
          this.$emit("extraVisitsAfter", null);
        }
      }
    },
    isDataChanged: {
      immediate: true,
      handler() {
        if (this.isDataChanged && this.extraVisitsAfter === 0) {
          this.$emit("dataEdit", {
            service_id: this.services.data_edit.value,
            sum_no_vat: this.dataEditSum,
            sum_with_vat: this.addVat(this.dataEditSum),
            quantity: 1
          });
        } else {
          this.$emit("dataEdit", null);
        }
      }
    },
    additionalSum: {
      immediate: true,
      handler() {
        this.$emit("additionalSumChange", this.additionalSum);
      }
    },
    "value.order.release_mode": {
      immediate: true,
      handler() {
        if (this.isEdit && this.serviceId === 1) {
          if (
            this.model?.order?.release_mode !==
            this.oldModel?.order?.release_mode
          )
            this.changeReleaseService();
        }
      }
    }
  },

  computed: {
    extraVisitsAfter() {
      return Number(this.extraOutputs);
    },
    dataEditSum() {
      if (this.isDataChanged && this.extraVisitsAfter === 0) {
        return this.getServicePrice(this.services.data_edit);
      }
      return 0;
    },
    additionalSum() {
      return this.extraVisitsAfterSum + this.dataEditSum;
    },
    totalPayedServiceSUm() {
      if (this.isEdit) {
        return this.model.details.services?.reduce(
          (total: number, { paid_sum }: { paid_sum: number }) => {
            if (paid_sum) {
              return total + Number(paid_sum);
            }
            return total;
          },
          0
        );
      }
      return 0;
    },
    totalRemainedForPayServiceSUm() {
      if (this.isEdit) {
        return this.model.details.services?.reduce(
          (total: number, { remained_sum }: { remained_sum: number }) => {
            if (remained_sum) {
              return total + Number(remained_sum);
            }
            return total;
          },
          0
        );
      }
      return 0;
    },
    isDataChanged() {
      if (this.isEdit && this.oldModel && this.canAddExtraServices) {
        const model = { ...this.model };
        const firstModel = {
          order: model.order,
          documents: model.documents,
          goods: model.details.goods,
          countries: model.details.countries
        };
        const secondModel = {
          order: this.oldModel.order,
          documents: this.oldModel.documents,
          goods: this.oldModel.goods,
          countries: this.oldModel.countries
        };

        return !lodash.isEqual(firstModel, secondModel);
      }
      return false;
    },
    canAddExtraServices() {
      return this.model.order.status.alias === "issued";
    },
    goodsSum() {
      let sum = 0;
      for (const item of this.model.details.countries) {
        if (item.quantity * item.price) {
          sum += item.quantity * item.price;
        }
      }
      return sum;
    }
  },

  async mounted() {
    try {
      const services = await this.$API
        .services()
        .getListByServiceType(this.serviceId);

      for (const service of services) {
        this.orderServices[service.alias] = service;
      }

      this.model.details?.services?.forEach(
        (item: any) => (item.editable = this.orderServices[item.alias].editable)
      );
    } catch (e) {
      await this.$store.dispatch("alert/showError", e.message || e);
    }
  },

  methods: {
    checkServices() {
      const services: Array<any> = [];

      // if (!this.isEdit) {
      if (this.serviceId === 1) {
        if (this.model.order.client.type) {
          services.push({
            service_id: this.getServiceId(
              `${this.model.order.client.type}_normal`
            ),
            sum_no_vat: this.getServicePrice(
              this.orderServices[`${this.model.order.client.type}_normal`]
            ),
            sum_with_vat:
              this.model.order.client.type === "physical"
                ? this.getServicePrice(
                    this.orderServices[`${this.model.order.client.type}_normal`]
                  )
                : this.addVat(
                    this.getServicePrice(
                      this.orderServices[
                        `${this.model.order.client.type}_normal`
                      ]
                    )
                  ),
            quantity: 1,
            name: this.orderServices[`${this.model.order.client.type}_normal`]
              .text
          });
        }

        if (this.model.details.goods.length > 1) {
          services.push({
            service_id: this.getServiceId(`extra_positions`),
            sum_no_vat: this.getServicePrice(
              this.orderServices.extra_positions,
              this.model.details.goods.length,
              1
            ),
            sum_with_vat: this.addVat(
              this.getServicePrice(
                this.orderServices.extra_positions,
                this.model.details.goods.length,
                1
              )
            ),
            quantity: this.model.details.goods.length - 1,
            name: this.orderServices.extra_positions.text
          });
        }

        if (this.model.order.release_mode === "urgent") {
          services.push({
            service_id: this.getServiceId(
              `${this.model.order.client.type}_urgent`
            ),
            sum_no_vat: this.getServicePrice(
              this.orderServices[`${this.model.order.client.type}_urgent`]
            ),
            sum_with_vat: this.addVat(
              this.getServicePrice(
                this.orderServices[`${this.model.order.client.type}_urgent`]
              )
            ),
            quantity: 1,
            name: this.orderServices[`${this.model.order.client.type}_urgent`]
              .text
          });
        }

        if (this.model.details.outputs > 1) {
          services.push({
            service_id: this.getServiceId("extra_visit"),
            sum_no_vat: this.getServicePrice(
              this.orderServices.extra_visit,
              this.model.details.outputs - 1
            ),
            sum_with_vat: this.addVat(
              this.getServicePrice(
                this.orderServices.extra_visit,
                this.model.details.outputs - 1
              )
            ),
            quantity: this.model.details.outputs - 1,
            name: this.orderServices.extra_visit.text
          });
        }

        if (this.model.order.for_cci_exposition) {
          const basicSum = this.getServicePrice(
            this.orderServices[`${this.model.order.client.type}_normal`]
          );
          const price = -(
            basicSum -
            (basicSum *
              (100 -
                this.getServicePrice(
                  this.orderServices.ata_exposition_discount
                ))) /
              100
          );
          services.push({
            service_id: this.getServiceId("ata_exposition_discount"),
            sum_no_vat: price,
            sum_with_vat: price,
            quantity: 1,
            name: this.orderServices.ata_exposition_discount.text
          });
        }
      } else if (this.serviceId === 2) {
        services.push({
          service_id: this.orderServices.aaij.value,
          sum_no_vat: this.getServicePrice(this.orderServices.aaij),
          sum_with_vat: this.addVat(
            this.getServicePrice(this.orderServices.aaij)
          ),
          quantity: 1,
          name: this.orderServices.aaij.name
        });
      }
      // }

      this.servicesTotalSum = services.reduce(
        (total: number, { sum_no_vat }: { sum_no_vat: number }) => {
          if (sum_no_vat) {
            return total + Number(sum_no_vat);
          }
          return total;
        },
        0
      );

      this.$emit("getServices", services);
      this.$emit("input", this.servicesTotalSum);
    },
    addVat(sum: number): number {
      return sum + (sum * 20) / 100;
    },
    setGuaranteePrice(): void {
      if (this.model.details.guaranty_required) {
        this.model.details.required_guaranty_sum =
          Math.trunc(0.3 * this.goodsSum) || 0;
      }
    },
    getServicePrice(service: any, value = 1, minusCount = 0) {
      if (service && service.values) {
        return (
          Number(
            service.values.find((el: any) => value >= el.min && value <= el.max)
              ?.value
          ) *
          (value - minusCount)
        );
      }
      return 0;
    },
    getServiceId(key: string) {
      if (this.orderServices && this.orderServices[key]) {
        return this.orderServices[key].value;
      }
    },
    async getReceipt() {
      try {
        const response = await this.$API
          .invoices()
          .geFileForPreview(this.model.order.id, { responseType: "blob" });

        const blob = new Blob([response.data], { type: "application/pdf" });
        const link = document.createElement("a");
        link.href = URL.createObjectURL(blob);
        link.target = "_blank";
        link.click();
        URL.revokeObjectURL(link.href);
      } catch (e) {
        await this.$store.dispatch("alert/showError", e.message);
      }
    },
    async previewReceipt() {
      try {
        const file = await this.$API
          .invoices()
          .getFileForPrint(this.model.order.id);
        if (file && file.url) {
          this.downloadFile(file);
        }
      } catch (e) {
        await this.$store.dispatch("alert/showError", e.message);
      }
    },
    toggleDeleteService(service: any): void {
      service._isDeleted = !service._isDeleted;
      if (service.alias === "ata_exposition_discount") {
        this.model.order.for_cci_exposition = !service._isDeleted;
      }

      this.$forceUpdate();
    },
    can(action: string, subject: string): boolean {
      return (
        permissions.actions.includes(action) &&
        !permissions.subject.includes(subject)
      );
    },
    downloadFile(file: any) {
      const element = document.createElement("a");
      element.setAttribute("href", file.url);
      element.setAttribute("target", "_blank");
      element.click();
    },
    changeReleaseService(): void {
      const service = this.services[`${this.model.order?.client?.type}_urgent`];

      if (service && this.model.order.release_mode === "urgent") {
        this.model.details.services.push({
          name: service.text,
          service_id: service.value,
          sum_no_vat: this.getServicePrice(service),
          sum_with_vat: this.addVat(this.getServicePrice(service)),
          quantity: 1
        });
      } else if (service && this.model.order.release_mode === "normal") {
        const serviceIndex = this.model.details.services.findIndex(
          (item: any) => item.alias === `${this.model.order.client.type}_urgent`
        );
        if (serviceIndex !== -1) {
          this.model.details.services?.splice(serviceIndex, 1);
        }
      }
    },
    updateVat(index: number): void {
      this.model.details.services[index].sum_with_vat = this.addVat(
        Number(this.model.details.services[index].sum_no_vat)
      );
    }
  }
});
